export default {
	data() {
		return {
			listStatus:"loading",
			searchVal: "",
			statusArr: [
				"全部",
				"未付款",
				"已付款"
			],
			status: "按状态筛选"
		}
	},
	methods: {
		search() {

		},

		statusPicker: function(e) {
			const that = this;
			that.status = that.statusArr[e.detail.value];
		},
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/vacate/detail",
				query:{
					id:id
				}
			})
		},
		clear(index) {
			const that = this;
			if (index == 0) {
				that.date = "按日期筛选";
			} else {
				that.star = "按评分筛选";
			}
		}
	}
}
